import { useCallback, useRef, useState } from "react";

export type ParamsType<T extends (...args: any) => any> = T extends (...args: infer P) => any ? P : any;

/**
 * é˜²æŠ–
 * @param fn ç›®æ ‡å‡½æ•°
 * @param delay å»¶è¿Ÿæ—¶é—´
 */
export function debounce<F extends (...args: any) => void>(fn: F, delay = 200) {
  let timer;
  return function (...args: ParamsType<F>) {
    clearTimeout(timer);
    timer = setTimeout(() => {
      fn.apply(this, args);
    }, delay);
  };
}

/**
 * é˜²æŠ–é’©å­
 *
 * é˜²æŠ–é’©å­ä½¿ç”¨ hooks è¿”å›žä¸€ä¸ªé˜²æŠ–å‡½æ•°ï¼Œå®ƒä¸åº”è¯¥ä½œç”¨äºŽå‰¯ä½œç”¨ä¸­ï¼Œä½ åº”è¯¥æŠŠå®ƒä½œç”¨åˆ°äº‹ä»¶æˆ–è€…åŠŸèƒ½å‡½æ•°ä¸Šã€‚
 *
 * @param cb ç›®æ ‡å‡½æ•°ï¼Œè¯¥å€¼å¯ä»¥ä½¿ç”¨ useCallback ç”Ÿæˆï¼Œä»¥é˜²æ­¢æ€§èƒ½æ¶ˆè€—ï¼Œå¦‚ä¸ä½¿ç”¨ useCallback å¹¶æ— ä½¿ç”¨å½±å“
 * @param delay å»¶è¿Ÿæ—¶é—´
 * @returns è¿”å›žä¸€ä¸ªé˜²æŠ–å‡½æ•°
 */
export function useDebounce<F extends (...args: any) => void>(cb: F, delay = 200) {
  // å®šæ—¶å™¨æŒ‡é’ˆï¼Œä¿è¯é˜²æŠ–å‡½æ•°çš„æ›´æ–°ä¸ä¼šä¸¢å¤±å®šæ—¶å™¨æŒ‡é’ˆ
  const [timer, setTimer] = useState<NodeJS.Timeout>(null);

  // æœ€æ–°çš„å‡½æ•°å‚è€ƒï¼Œä¿è¯åŽç»­æ‰§è¡Œå‡½æ•°æ—¶æ€»æ˜¯ä½¿ç”¨æœ€æ–°çš„å‚æ•°å¼•ç”¨ï¼Œè€Œä¸æ˜¯è¿‡æ—¶çš„
  const cbRef = useRef(cb);
  cbRef.current = cb;

  return useCallback(
    function (...args: ParamsType<F>) {
      clearTimeout(timer);
      setTimer(setTimeout(() => cbRef.current.apply(this, args), delay));
    },
    [timer, delay]
  );
}

/**
 * äº‹ä»¶èŠ‚æµï¼Œä»…åœ¨ä¸‹ä¸€å¸§æ¸²æŸ“æ—¶å†æ¬¡æ‰§è¡Œäº‹ä»¶ï¼Œä¸€èˆ¬ç”¨äºŽä¼˜åŒ– resizeï¼Œscroll ç­‰äº‹ä»¶
 * å¦‚æ·»åŠ è‡ªå®šä¹‰äº‹ä»¶ optimizedResize ä»£æ›¿ resize äº‹ä»¶å†™ä¸º throttleEvent('resize','optimizedResize')
 * @reference https://developer.mozilla.org/zh-CN/docs/Web/API/Window/resize_event
 */
export function throttleEvent(type: string, name: string, obj: EventTarget = window) {
  let running = false;

  function func() {
    if (running) return;
    running = true;

    requestAnimationFrame(function () {
      obj.dispatchEvent(new CustomEvent(name));
      running = false;
    });
  }

  obj.addEventListener(type, func);
}

export const isBrowser = typeof window !== "undefined";
